import { DocumentsCollectionEventInfo } from "@rp/react/tanstack-api/documents"
import { Box, Typography } from "@rp/react/web/mui/core"
import { Apartment } from "@rp/react/web/mui/icons"

type DocumentsCollectionCompactCompanyInfoProps = {
  eventInfo: DocumentsCollectionEventInfo | null
}

const DocumentsCollectionCompactCompanyInfo = ({
  eventInfo,
}: DocumentsCollectionCompactCompanyInfoProps) => (
  <Box sx={{ display: "flex", alignItems: "center" }}>
    {eventInfo?.entity.logoUrl ? (
      <img src={eventInfo.entity.logoUrl} alt={eventInfo.entity.name} height="22px" width="22px" />
    ) : (
      <Apartment sx={{ height: "20px", width: "20px" }} />
    )}

    <Box sx={{ textAlign: "left", ml: 0.5 }}>
      <Typography
        color="text.secondary"
        fontSize="11px"
        lineHeight="16px"
        overflow="hidden"
        noWrap
        maxWidth="150px"
      >
        {eventInfo?.entity.ticker || eventInfo?.entity.name}
      </Typography>

      <Typography fontSize="11px" lineHeight="16px" maxWidth="150px" overflow="hidden" noWrap>
        {eventInfo?.event}
      </Typography>
    </Box>
  </Box>
)

export { DocumentsCollectionCompactCompanyInfo }
